import NavMenu from "./NavMenu";
import "../Styles/General.css";
import "../Styles/Donate.css";
import ReactModal from "react-modal";
import React, { useState } from "react";

function Donate() {
  const [donationSubmitted, setDonationSubmitted] = useState(false);
  const [confirmed, setConfirmed] = useState(false);
  const [amount, setAmount] = useState("");
  const [email, setEmail] = useState("");

  const handleSubmitDonation = (event) => {
    event.preventDefault();
    const formValid =
      document.getElementById("amount").checkValidity() &&
      document.getElementById("firstName").checkValidity() &&
      document.getElementById("lastName").checkValidity() &&
      document.getElementById("phoneNumber").checkValidity() &&
      document.getElementById("email").checkValidity() &&
      document.getElementById("streetAddress").checkValidity() &&
      document.getElementById("city").checkValidity() &&
      document.getElementById("country").checkValidity() &&
      document.getElementById("postalCode").checkValidity() &&
      document.getElementById("cardNumber").checkValidity() &&
      document.getElementById("cvv").checkValidity() &&
      document.getElementById("month").checkValidity() &&
      document.getElementById("year").checkValidity();
    if (formValid) {
      setDonationSubmitted(true);
    } else {
      alert("Please fill all required fields.");
    }
  };

  const handleCloseDonationConfirmation = () => {
    setDonationSubmitted(false);
  };

  const handleConfirmSubmitDonation = () => {
    setDonationSubmitted(false);
    setConfirmed(true);
  };

  const handleCloseConfirmSubmitDonation = () => {
    window.location.href = "/SEG3125Project2/";
  };

  const handleAmountChange = (event) => {
    event.preventDefault();
    setAmount(event.target.value);
  };
  const handleEmailChange = (event) => {
    event.preventDefault();
    setEmail(event.target.value);
  };
  return (
    <div className="page-setup">
      <div className="donate-content">
        <h1 className="title">DONATE</h1>
        <hr />
        <form>
          <br></br>
          <p className="red left-align">
            Thank you for supporting the Museum of Photography
          </p>
          <br></br>
          <div className="row">
            <div className="col-6">
              <img
                className="donate-img "
                src={require("../Images/donation-wall.jpg")}
                alt="Sample Image"
              />
            </div>
            <div className="col-6">
              <h4>AMOUNT</h4>
              <label htmlFor="amount">$</label>
              <input
                id="amount"
                type="number"
                className="donate-input"
                placeholder="Enter amount"
                value={amount}
                onChange={handleAmountChange}
                required
              />

              <h4>CONTACT INFORMATION</h4>
              <input
                id="firstName"
                type="text"
                className="donate-input"
                placeholder="First Name"
                required
              />
              <input
                id="lastName"
                type="text"
                className="donate-input"
                placeholder="Last Name"
                required
              />
              <br></br>
              <input
                id="phoneNumber"
                type="tel"
                className="donate-input"
                placeholder="Phone Number"
                required
              />
              <input
                id="email"
                type="email"
                className="donate-input"
                placeholder="Email"
                value={email}
                onChange={handleEmailChange}
              />
              <br></br>
              <input
                id="streetAddress"
                type="text"
                className="donate-input"
                placeholder="Street Address"
                required
              />
              <input type="text" className="donate-input" placeholder="Apt" />
              <br></br>
              <input
                id="city"
                type="text"
                className="donate-input"
                placeholder="City"
                required
              />
              <input
                id="country"
                type="text"
                className="donate-input"
                placeholder="Country"
                required
              />
              <br></br>
              <input
                id="postalCode"
                type="text"
                className="donate-input"
                placeholder="Postal Code"
                required
              />
              <br></br>
              <label>
                <input type="checkbox" />
                Make donation anonymous
              </label>
              <br></br>
              <br></br>

              <h4>PAYMENT INFORMATION</h4>
              <input
                id="cardNumber"
                type="number"
                className="donate-input"
                placeholder="Card Number"
                required
              />
              <input
                id="cvv"
                type="text"
                className="donate-input"
                placeholder="CVV"
                required
              />
              <br></br>
              <select id="month" className="donate-input" required>
                <option value="">Month</option>
                <option value="Jan">01</option>
                <option value="Feb">02</option>
                <option value="Mar">03</option>
                <option value="Apr">04</option>
                <option value="May">05</option>
                <option value="Jun">06</option>
                <option value="Jul">07</option>
                <option value="Aug">08</option>
                <option value="Sep">09</option>
                <option value="Oct">10</option>
                <option value="Nov">11</option>
                <option value="Dec">12</option>
              </select>
              <input
                id="year"
                type="number"
                className="donate-input"
                placeholder="Year"
                required
              />
              <br></br>
              <input
                type="submit"
                value="DONATE"
                className="red-button"
                onClick={handleSubmitDonation}
              ></input>
            </div>
          </div>
        </form>

        <ReactModal
          isOpen={donationSubmitted}
          onRequestClose={handleCloseDonationConfirmation}
          style={{
            content: {
              width: "50vw",
              height: "30vw",
              margin: "auto",
              padding: "0",
            },
          }}
        >
          <div className="modal-content pop-up">
            <br></br>
            <br></br>
            <h3>Domation Summary</h3>
            <p>Amount: ${amount}</p>
            <button
              className="close-button"
              onClick={handleCloseDonationConfirmation}
            >
              X
            </button>
            <br></br>
            <br></br>
            <button className="button" onClick={handleConfirmSubmitDonation}>
              Confirm Donation
            </button>
          </div>
        </ReactModal>

        <ReactModal
          isOpen={confirmed}
          onRequestClose={handleCloseConfirmSubmitDonation}
          style={{
            content: {
              width: "50vw",
              height: "30vw",
              margin: "auto",
              padding: "0",
            },
          }}
        >
          <div className="modal-content pop-up">
            <br></br>
            <br></br>
            <h3>Thank you for your donation!</h3>
            <p>
              The Museum of Photography greatly appreciates your support!
              <br></br>A confirmation email has been sent to {email}
            </p>
            <br></br>
            <button
              className="close-button"
              onClick={handleCloseConfirmSubmitDonation}
            >
              X
            </button>
          </div>
        </ReactModal>
      </div>
      <NavMenu />
    </div>
  );
}

export default Donate;
