import "../Styles/General.css";
import "bootstrap/dist/css/bootstrap.css";
import React from "react";

const CollectionCard = ({ title, year, photographer, image }) => {
  return (
    <div className="col-6 collection-item">
      <div className="image-container">
        <img src={image} alt="Sample Image" />
      </div>
      <div className="card" style={{ padding: "0.5vw 0.5vw 0.5vw 0.5vw" }}>
        <div className="row">
          <div className="col-6">
            <h2>{title}</h2>
          </div>
          <div className="col-6">
            <p style={{ textAlign: "right" }}>{year}</p>
          </div>
        </div>
        <h5>{photographer}</h5>
      </div>
    </div>
  );
};

export default CollectionCard;
