import React from 'react';
import "../Styles/General.css"
import "../Styles/VisitNav.css"
import { HashLink as Link } from 'react-router-hash-link';



function VisitNav() {
  return (
    <div className="visit-nav">
        <h3 className='jump-to'>JUMP TO</h3>
        <ul>
        <li>
          <Link to="/visit#admission">Admission</Link>
        </li>
        <li>
          <Link to="/visit#tour-schedule">Tour Schedule</Link>
        </li>
        <li>
          <Link to="/visit#museum-map">Museum Map</Link>
        </li>
        <li>
          <Link to="/visit#hours">Hours</Link>
        </li>
        <li>
          <Link to="/visit#location">Location</Link>
        </li>
        <li>
          <Link to="/visit#faq">FAQ</Link>
        </li>
      </ul>
    </div>
  );
}

export default VisitNav;
