import React, { useState, useEffect } from "react";
import NavMenu from "./NavMenu";
import "../Styles/General.css";
import "bootstrap/dist/css/bootstrap.css";
import "../Styles/Collection.css";
import CollectionPage from "./CollectionPage";

function Collection() {
  const cardsData = [
    {
      title: "Africa Blues",
      year: "2023",
      photographer: "Edoardo Delille & Giulia Piermartiri",
      image: require("../Images/Africa-Blues/afrcia1.jpg"),
    },
    {
      title: "Africa Blues",
      year: "2023",
      photographer: "Edoardo Delille & Giulia Piermartiri",
      image: require("../Images/Africa-Blues/africa2.jpg"),
    },
    {
      title: "Africa Blues",
      year: "2023",
      photographer: "Edoardo Delille & Giulia Piermartiri",
      image: require("../Images/Africa-Blues/africa3.jpg"),
    },
    {
      title: "Africa Blues",
      year: "2023",
      photographer: "Edoardo Delille & Giulia Piermartiri",
      image: require("../Images/Africa-Blues/africa4.jpg"),
    },

    {
      title: "Athen's Studio",
      year: "2017",
      photographer: "Diego Mayon",
      image: require("../Images/Athens-Studio/Brothel1.jpg"),
    },
    {
      title: "Athen's Studio",
      year: "2017",
      photographer: "Diego Mayon",
      image: require("../Images/Athens-Studio/Brothel2.jpg"),
    },
    {
      title: "Athen's Studio",
      year: "2017",
      photographer: "Diego Mayon",
      image: require("../Images/Athens-Studio/Brothel3.jpg"),
    },
    {
      title: "Athen's Studio",
      year: "2017",
      photographer: "Diego Mayon",
      image: require("../Images/Athens-Studio/Brothel4.jpg"),
    },

    {
      title: "Billions of Synchronous Fireflies Light up a Tiger Reserve",
      year: "2023",
      photographer: "Sriram Murali",
      image: require("../Images/Billions-Fireflies/fireflies1.jpg"),
    },
    {
      title: "Billions of Synchronous Fireflies Light up a Tiger Reserve",
      year: "2023",
      photographer: "Sriram Murali",
      image: require("../Images/Billions-Fireflies/fireflies2.jpg"),
    },
    {
      title: "Billions of Synchronous Fireflies Light up a Tiger Reserve",
      year: "2023",
      photographer: "Sriram Murali",
      image: require("../Images/Billions-Fireflies/fireflies3.jpg"),
    },
    {
      title: "Billions of Synchronous Fireflies Light up a Tiger Reserve",
      year: "2023",
      photographer: "Sriram Murali",
      image: require("../Images/Billions-Fireflies/fireflies4.jpg"),
    },

    {
      title: "Inheritor",
      year: "2021",
      photographer: "Yanan Li",
      image: require("../Images/Inheritor/inheritor1.jpg"),
    },
    {
      title: "Inheritor",
      year: "2021",
      photographer: "Yanan Li",
      image: require("../Images/Inheritor/inheritor2.jpg"),
    },
    {
      title: "Inheritor",
      year: "2021",
      photographer: "Yanan Li",
      image: require("../Images/Inheritor/inheritor3.jpg"),
    },
    {
      title: "Inheritor",
      year: "2021",
      photographer: "Yanan Li",
      image: require("../Images/Inheritor/inheritor4.jpg"),
    },

    {
      title: "Quarrie's Men",
      year: "2017",
      photographer: "Nader Saadallah",
      image: require("../Images/Quarrie's-men/quarrie1.jpg"),
    },
    {
      title: "Quarrie's Men",
      year: "2017",
      photographer: "Nader Saadallah",
      image: require("../Images/Quarrie's-men/quarrie2.jpg"),
    },
    {
      title: "Quarrie's Men",
      year: "2017",
      photographer: "Nader Saadallah",
      image: require("../Images/Quarrie's-men/quarrie3.jpg"),
    },
    {
      title: "Quarrie's Men",
      year: "2017",
      photographer: "Nader Saadallah",
      image: require("../Images/Quarrie's-men/quarrie4.jpg"),
    },

    {
      title: "Shaolin Kung Fu",
      year: "2017",
      photographer: "Luo Pin Xi",
      image: require("../Images/Shaolin-Kung-Fu/martial-arts1.jpg"),
    },
    {
      title: "Shaolin Kung Fu",
      year: "2017",
      photographer: "Luo Pin Xi",
      image: require("../Images/Shaolin-Kung-Fu/martial-arts2.jpg"),
    },
    {
      title: "Shaolin Kung Fu",
      year: "2017",
      photographer: "Luo Pin Xi",
      image: require("../Images/Shaolin-Kung-Fu/martial-arts3.jpg"),
    },
    {
      title: "Shaolin Kung Fu",
      year: "2017",
      photographer: "Luo Pin Xi",
      image: require("../Images/Shaolin-Kung-Fu/martial-arts4.jpg"),
    },
  ];

  const rowsPerPage = 6;
  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedFilters, setSelectedFilters] = useState({
    Photographer: [],
    Exhibition: [],
    Year: [],
  });
  const [filteredData, setFilteredData] = useState([]);

  const startIndex = (currentPage - 1) * rowsPerPage;
  const endIndex = startIndex + rowsPerPage;
  const totalPages = Math.ceil(filteredData.length / rowsPerPage);
  const currentPageData = filteredData.slice(startIndex, endIndex);

  useEffect(() => {
    filterAndSearch(searchQuery);
  }, [searchQuery, selectedFilters]);

  const handleSearch = (event) => {
    const query = event.target.value;
    setSearchQuery(query);
  };

  const handleFilterSelection = (event) => {
    const selectedOption = event.target.value;
    const filterType = event.target.dataset.filterType;
    const isChecked = event.target.checked;

    setSelectedFilters((prevFilters) => {
      const updatedFilters = { ...prevFilters };

      if (isChecked) {
        updatedFilters[filterType].push(selectedOption);
      } else {
        const index = updatedFilters[filterType].indexOf(selectedOption);
        if (index !== -1) {
          updatedFilters[filterType].splice(index, 1);
        }
      }

      return updatedFilters;
    });

    filterAndSearch(searchQuery);
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const filterAndSearch = (query) => {
    let filteredCards = cardsData;

    if (selectedFilters.Photographer.length > 0) {
      filteredCards = filteredCards.filter((card) =>
        selectedFilters.Photographer.includes(card.photographer)
      );
    }

    if (selectedFilters.Exhibition.length > 0) {
      filteredCards = filteredCards.filter((card) =>
        selectedFilters.Exhibition.includes(card.title)
      );
    }

    if (selectedFilters.Year.length > 0) {
      filteredCards = filteredCards.filter((card) =>
        selectedFilters.Year.includes(card.year)
      );
    }

    // Apply search query
    if (query) {
      const searchQuery = query.toLowerCase();
      filteredCards = filteredCards.filter(
        (card) =>
          card.title.toLowerCase().includes(searchQuery) ||
          card.photographer.toLowerCase().includes(searchQuery) ||
          card.year === searchQuery
      );
    }

    console.log("filteredCards:", filteredCards); // Check the value of filteredCards

    setFilteredData(filteredCards);
    setCurrentPage(1);
  };

  console.log("filteredData:", filteredData);
  console.log("currentPageData:", currentPageData);
  console.log("totalPages:", totalPages);

  return (
    <div className="inverse-page-setup">
      <div className="page-content">
        <h1 className="title">COLLECTION</h1>
        <hr />
        <br />
        <p>
          Explore the entire collection of photographs the Museum of Photogaphy
          has ever displayed
        </p>
        <br />
        <div className="row">
          <div className="col-12">
            <input
              className="search-bar"
              type="text"
              placeholder="Search Collection"
              value={searchQuery}
              onChange={handleSearch}
            />
            <br />
            <br />
          </div>
        </div>
        <div className="row">
          <div className="col-3">
            <div className="filter-container">
              <form onChange={(event) => handleFilterSelection(event)}>
                <h4>Exhibition</h4>

                <label htmlFor="option1" className="checkbox-label">
                  <input
                    id="option1"
                    value="Africa Blues"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Africa Blues
                </label>
                <br />

                <label htmlFor="option2" className="checkbox-label">
                  <input
                    id="option2"
                    value="Athen's Studio"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Athen's Studio
                </label>
                <br />

                <label htmlFor="option3" className="checkbox-label">
                  <input
                    id="option3"
                    value="Billions of Synchronous Fireflies Light up a Tiger Reserve"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Billions of Synchronous Fireflies Light up a Tiger Reserve
                </label>
                <br />

                <label htmlFor="option4" className="checkbox-label">
                  <input
                    id="option4"
                    value="Inheritor"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Inheritor
                </label>
                <br />

                <label htmlFor="option5" className="checkbox-label">
                  <input
                    id="option5"
                    value="Quarrie's Men"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Quarrie's Men
                </label>
                <br />

                <label htmlFor="option6" className="checkbox-label">
                  <input
                    id="option6"
                    value="Shaolin Kung Fu"
                    type="checkbox"
                    data-filter-type="Exhibition"
                  />
                  Shaolin Kung Fu
                </label>
                <br />
                <br />

                <h4>Photographer</h4>
                <label htmlFor="option7" className="checkbox-label">
                  <input
                    id="option7"
                    value="Edoardo Delille & Giulia Piermartiri"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Edoardo Delille
                </label>
                <br />

                <label htmlFor="option8" className="checkbox-label">
                  <input
                    id="option8"
                    value="Yanan Li"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Yanan Li
                </label>
                <br />

                <label htmlFor="option9" className="checkbox-label">
                  <input
                    id="option9"
                    value="Diego Mayon"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Diego Mayon
                </label>
                <br />

                <label htmlFor="option10" className="checkbox-label">
                  <input
                    id="option10"
                    value="Sriram Murali"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Sriram Murali
                </label>
                <br />

                <label htmlFor="option11" className="checkbox-label">
                  <input
                    id="option11"
                    value="Edoardo Delille & Giulia Piermartiri"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Giulia Piermartiri
                </label>
                <br />

                <label htmlFor="option12" className="checkbox-label">
                  <input
                    id="option12"
                    value="Nader Saadallah"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Nader Saadallah
                </label>
                <br />

                <label htmlFor="option13" className="checkbox-label">
                  <input
                    id="option13"
                    value="Luo Pin Xi"
                    type="checkbox"
                    data-filter-type="Photographer"
                  />
                  Luo Pin Xi
                </label>
                <br />
                <br />

                <h4>Year</h4>
                <label htmlFor="option14" className="checkbox-label">
                  <input
                    id="option14"
                    value="2017"
                    type="checkbox"
                    data-filter-type="Year"
                  />
                  2017
                </label>
                <br />

                <label htmlFor="option15" className="checkbox-label">
                  <input
                    id="option15"
                    value="2021"
                    type="checkbox"
                    data-filter-type="Year"
                  />
                  2021
                </label>
                <br />

                <label htmlFor="option16" className="checkbox-label">
                  <input
                    id="option16"
                    value="2023"
                    type="checkbox"
                    data-filter-type="Year"
                  />
                  2023
                </label>
              </form>
            </div>
          </div>
          <div className="col-9">
            <CollectionPage cardsData={currentPageData} />
            <div className="pagination">
              {Array.from({ length: totalPages }, (_, index) => index + 1).map(
                (page) => (
                  <button
                    key={page}
                    className={currentPage === page ? "active" : ""}
                    onClick={() => handlePageChange(page)}
                  >
                    {page}
                  </button>
                )
              )}
            </div>
          </div>
        </div>
        <br />
        <br />
      </div>
      <NavMenu />
    </div>
  );
}

export default Collection;
