import React, { useState } from "react";
import NavMenu from "./NavMenu";
import CalendarInput from "./CalendarInput";
import "bootstrap/dist/css/bootstrap.css";
import "../Styles/General.css";
import "../Styles/Tickets.css";
import ReactModal from "react-modal";

function Tickets() {
  const [childTicketCount, setChildTicketCount] = useState(0);
  const [studentTicketCount, setStudentTicketCount] = useState(0);
  const [adultTicketCount, setAdultTicketCount] = useState(0);
  const [seniorTicketCount, setSeniorTicketCount] = useState(0);
  const [email, setEmail] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [purchaseConfirmed, setPurchaseConfirmed] = useState(false);

  const childTicketPrice = 0; // Set the actual price for child tickets
  const studentTicketPrice = 5.0; // Set the actual price for student tickets
  const adultTicketPrice = 9.0; // Set the actual price for adult tickets
  const seniorTicketPrice = 6.0; // Set the actual price for senior tickets

  const calculatedTotal =
    childTicketCount * childTicketPrice +
    studentTicketCount * studentTicketPrice +
    adultTicketCount * adultTicketPrice +
    seniorTicketCount * seniorTicketPrice;

  const handleChildTicketCountChange = (event) => {
    event.preventDefault();
    const count = parseInt(event.target.value);
    setChildTicketCount(count);
  };

  const handleStudentTicketCountChange = (event) => {
    event.preventDefault();
    const count = parseInt(event.target.value);
    setStudentTicketCount(count);
  };

  const handleAdultTicketCountChange = (event) => {
    event.preventDefault();
    const count = parseInt(event.target.value);
    setAdultTicketCount(count);
  };

  const handleSeniorTicketCountChange = (event) => {
    event.preventDefault();
    const count = parseInt(event.target.value);
    setSeniorTicketCount(count);
  };

  const handleDecrement = (setter) => {
    setter((prevCount) => (prevCount > 0 ? prevCount - 1 : 0));
  };

  const handleIncrement = (setter) => {
    setter((prevCount) => prevCount + 1);
  };

  const handleEmailChange = (event) => {
    event.preventDefault();
    setEmail(event.target.value);
  };

  const handlePurchaseTickets = (event) => {
    event.preventDefault();
    const isFormValid =
      document.getElementById("firstName").checkValidity() &&
      document.getElementById("lastName").checkValidity() &&
      document.getElementById("phoneNumber").checkValidity() &&
      document.getElementById("email").checkValidity() &&
      document.getElementById("streetAddress").checkValidity() &&
      document.getElementById("city").checkValidity() &&
      document.getElementById("country").checkValidity() &&
      document.getElementById("postalCode").checkValidity() &&
      document.getElementById("cardNumber").checkValidity() &&
      document.getElementById("cvv").checkValidity() &&
      document.getElementById("expirationMonth").checkValidity() &&
      document.getElementById("expirationYear").checkValidity();

    if (isFormValid) {
      setShowModal(true);
    } else {
      alert("Please fill out all fields.");
    }
  };

  const handleConfirmPurchase = () => {
    setShowModal(false);
    setPurchaseConfirmed(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setPurchaseConfirmed(false);
  };

  const handleCloseConfirmation = () => {
    window.location.href = "/SEG3125Project2/";
  };

  return (
    <div className="page-setup">
      <div className="page-content">
        <h1 className="title">TICKETS</h1>
        <hr />
        <br />
        <h3>TICKETS</h3>
        <br />
        <h4 className="red">A ticket gives you access to all exhibitions.</h4>
        <br />
        <br />
        <form>
          <div className="row">
            <div className="col-6">
              <h4>Child</h4>
              <p>Age 11 and under</p>
            </div>
            <div className="col-2">
              <h4>Free</h4>
            </div>
            <div className="col-4">
              <div className="counter">
                <button onClick={() => handleDecrement(setChildTicketCount)}>
                  -
                </button>
                <input
                  type="number"
                  className="ticket-input"
                  value={childTicketCount}
                  onChange={handleChildTicketCountChange}
                />
                <button onClick={() => handleIncrement(setChildTicketCount)}>
                  +
                </button>
              </div>
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-6">
              <h4>Student</h4>
              <p>Age 18 and under</p>
            </div>
            <div className="col-2">
              <h4>$5.00</h4>
            </div>
            <div className="col-4">
              <div className="counter">
                <button onClick={() => handleDecrement(setStudentTicketCount)}>
                  -
                </button>
                <input
                  type="number"
                  className="ticket-input"
                  value={studentTicketCount}
                  onChange={handleStudentTicketCountChange}
                />
                <button onClick={() => handleIncrement(setStudentTicketCount)}>
                  +
                </button>
              </div>
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-6">
              <h4>Adult</h4>
              <br />
            </div>
            <div className="col-2">
              <h4>$9.00</h4>
            </div>
            <div className="col-4">
              <div className="counter">
                <button onClick={() => handleDecrement(setAdultTicketCount)}>
                  -
                </button>
                <input
                  type="number"
                  className="ticket-input"
                  value={adultTicketCount}
                  onChange={handleAdultTicketCountChange}
                />
                <button onClick={() => handleIncrement(setAdultTicketCount)}>
                  +
                </button>
              </div>
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-6">
              <h4>Senior</h4>
              <p>Age 60+</p>
            </div>
            <div className="col-2">
              <h4>$6.00</h4>
            </div>
            <div className="col-4">
              <div className="counter">
                <button onClick={() => handleDecrement(setSeniorTicketCount)}>
                  -
                </button>
                <input
                  type="number"
                  className="ticket-input"
                  value={seniorTicketCount}
                  onChange={handleSeniorTicketCountChange}
                />
                <button onClick={() => handleIncrement(setSeniorTicketCount)}>
                  +
                </button>
              </div>
            </div>
          </div>
          <br />
          <h3>DATE</h3>
          <br />
          <h4 className="red">
            You may visit anytime during operating hours on your selected date.
          </h4>
          <br />
          <CalendarInput />
          <br />
          <br />

          <div className="row">
            <div className="col-6">
              <h3>CONTACT INFORMATION</h3>
              <div className="row">
                <h4 className="red">
                  Your tickets will be sent to the provided email address.
                </h4>
              </div>
              <input
                id="firstName"
                type="text"
                className="checkout-input"
                placeholder="First Name"
                required
              />
              <input
                id="lastName"
                type="text"
                className="checkout-input"
                placeholder="Last Name"
                required
              />
              <br />
              <input
                id="phoneNumber"
                type="text"
                className="checkout-input"
                placeholder="Phone Number"
                required
              />
              <input
                id="email"
                type="text"
                value={email}
                onChange={handleEmailChange}
                className="checkout-input"
                placeholder="Email"
                required
              />
              <br />
              <input
                id="streetAddress"
                type="text"
                className="checkout-input"
                placeholder="Street Address"
                required
              />
              <input type="text" className="checkout-input" placeholder="Apt" />
              <br />
              <input
                id="city"
                type="text"
                className="checkout-input"
                placeholder="City"
                required
              />
              <input
                id="country"
                type="text"
                className="checkout-input"
                placeholder="Country"
                required
              />
              <input
                id="postalCode"
                type="text"
                className="checkout-input"
                placeholder="Postal Code"
                required
              />
            </div>
            <div className="col-6">
              <h3>PAYMENT INFORMATION</h3>
              <br />
              <input
                id="cardNumber"
                type="number"
                className="checkout-input"
                placeholder="Card Number"
                required
              />
              <input
                id="cvv"
                type="number"
                className="checkout-input"
                placeholder="CVV"
                required
              />
              <br />
              <select className="checkout-input">
                <option value="">Month</option>
                <option value="Jan">01</option>
                <option value="Feb">02</option>
                <option value="Mar">03</option>
                <option value="Apr">04</option>
                <option value="May">05</option>
                <option value="Jun">06</option>
                <option value="Jul">07</option>
                <option value="Aug">08</option>
                <option value="Sep">09</option>
                <option value="Oct">10</option>
                <option value="Nov">11</option>
                <option value="Dec">12</option>
              </select>
              <input
                id="expirationMonth"
                type="text"
                className="checkout-input"
                placeholder="Year"
                required
              />
            </div>
          </div>
          <input
            id="expirationYear"
            type="submit"
            className="red-button"
            onClick={handlePurchaseTickets}
            value="PURCHASE TICKETS"
          ></input>
        </form>

        <NavMenu />

        <ReactModal
          isOpen={showModal}
          onRequestClose={handleCloseModal}
          contentLabel="Ticket Summary"
          style={{
            content: {
              width: "50vw",
              height: "30vw",
              margin: "auto",
              padding: "0",
            },
          }}
        >
          <div className="modal-content pop-up">
            <h3>Summary</h3>
            {childTicketCount > 0 && <p>Child Tickets: {childTicketCount}</p>}
            {studentTicketCount > 0 && (
              <p>Student Tickets: {studentTicketCount}</p>
            )}
            {adultTicketCount > 0 && <p>Adult Tickets: {adultTicketCount}</p>}
            {seniorTicketCount > 0 && (
              <p>Senior Tickets: {seniorTicketCount}</p>
            )}
            <p>Total: ${calculatedTotal.toFixed(2)}</p>
            <button className="close-button" onClick={handleCloseModal}>
              X
            </button>
            <div className="button-container">
              <button
                className="button confirm"
                onClick={handleConfirmPurchase}
              >
                Confirm Purchase
              </button>
            </div>
          </div>
        </ReactModal>
        <ReactModal
          isOpen={purchaseConfirmed}
          onRequestClose={handleCloseModal}
          contentLabel="Purchase Confirmation"
          style={{
            content: {
              width: "50vw",
              height: "30vw",
              margin: "auto",
              padding: "0",
            },
          }}
        >
          <div className="modal-content pop-up">
            <h3>Purchase Successful</h3>
            <br></br>
            <p>Your tickets have been sent to {email}.</p>
            <button className="close-button" onClick={handleCloseConfirmation}>
              X
            </button>
          </div>
        </ReactModal>
      </div>
    </div>
  );
}

export default Tickets;
